@media (max-width: 400px) {


    #greet-head {
        font-size: 20px;
        text-align: center !important;
    }

    /* Make typewriter text responsive */
    #typewriter-div {
        font-size: 0.75rem;
        max-width: 80%;
        /* Limits the width to avoid overflow */
        display: block;
        /* Ensures it behaves as a block element */
        text-align: center;
        /* Keeps text centered */
        word-break: break-word;
        /* Ensures words break if needed */
        overflow-wrap: break-word;
        /* Forces breaking at the word level */
        white-space: normal;
        /* Allows text wrapping (important!) */
        margin-left: 10% !important;
        margin-right: 10% !important;
    }


    /* Adjust section headings */
    #count-head,
    #contact-head {
        font-size: 20px;
        margin-right: 0px !important;
    }

    #problem-span,
    #contact-span {
        font-size: 30px !important;
    }

    #contact-text,
    #problem-text {
        font-size: 16px;
    }

    /* Ensure wrapper div remains flexible */
    #wrapper-div {
        width: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
    }

    /* Make contact section more compact */
    #contact-div {
        width: 100%;
        margin: 0;
        text-align: center;
    }

    /* Adjust social icons */
    #social-div {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 8px;
        padding: 8px 0;
    }

    .social-icon {
        font-size: 35px;
    }

    /* Adjust image size */
    #photo {
        height: 220px;
        width: auto;
    }

    /* Ensure left and right divs take full width */
    #left-div,
    #right-div {
        width: 100% !important;
    }

    #left-div {
        margin-right: 15px !important;
    }

    #right-div {
        margin-top: 20px !important;
    }

    #social-div a {
        margin: 10px !important;
    }

    .coding-icon {
        font-size: 40px !important;
    }

    #coding-div h1 {
        font-size: 22px !important;
    }

    #coding-icons-div a {
        margin: 10px !important;
    }

    .project-div {
        height: 270px !important;
    }

    .project-description {
        height: 60px !important;
    }

    .project-title {
        height: 50px !important;
    }

    .timeline-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .timeline-item.left,
    .timeline-item.right {
        margin: 0px !important;
        text-align: center;
        justify-content: center !important;
        align-items: center !important;
        width: 50%;
        margin-bottom: 38% !important;
        margin-top: 10% !important;
        height: 100px !important;
    }

    .timeline-item.left {
        margin-right: 30% !important;
    }

    .timeline-item.right {
        margin-left: 30% !important;
    }

    .timeline-content {
        width: 100%;
        /* Adjust width for better readability */
    }

    .central-line {
        margin-top: -23px !important;
    }

    #outer-div h2 {
        font-size: 2rem !important;
        margin-top: 20%;
    }

    #contact {
        width: 60% !important;
        height: 300px !important;
    }

    #contact h2 {
        font-size: 24px !important;
        margin-top: -5px;
    }

    #contact input,
    textarea {
        padding: 10px !important;
    }

    #single-head {
        margin-top: 10% !important;
        font-size: 24px !important;
    }

    .status {
        text-align: center;
        align-items: center;
        justify-content: center;
        margin-left: 30% !important;
    }

    .left-div-project {
        width: auto !important;
        margin-left: 7% !important;
    }

    .detailed_description {
        display: none;
    }
    .demo_button{
        margin-top: 10% !important;
    }
    .status{
        margin: 0px !important;
        margin-left: 25% !important;
    }
}

